import React from "react";
import { Link } from "react-router-dom";
import "../css/navbar.css";

function NavBar() {
  return (
    <>
      <div class="navbar_header">
        {/*  */}
        <h1 class="logo">
          <Link to="/">TPMP</Link>
        </h1>
        {/*  */}
        <nav class="navbar_category">
          <ul id="navbar_main_category">
            <li>
              <Link to="/">Main</Link>
            </li>
            <li>
              <Link to="/board/list"></Link>
            </li>

            <li>
              <Link to="/create"></Link>
            </li>
            <li>
              <Link to="/login"></Link>
            </li>
          </ul>
        </nav>
      </div>
    </>
  );
}

export default NavBar;
