import "../css/board-create.css";

function CreateForm() {
  return (
    <div class="default_form">
      <form method="post" action="/board/create">
        <ul>
          <li>
            
            <input type="text" name="title" />
          </li>
          <li>
             
            <input type="text" name="skill_stack" />
          </li>
          <li>
            
            <input type="text" name="work_field" />
          </li>
          <li>
            <label for="max_people"> </label>
            <select name="max_people">
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              <option value="6">6</option>
              <option value="7">7</option>
              <option value="8">8 </option>
            </select>
          </li>
          <li>
            <label for="end_date"> </label>
            <select name="end_date">
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              <option value="6 ">6 </option>
            </select>
          </li>
        </ul>
        <textarea class="create_board" name="content"></textarea>
        <input class="submit" type="submit" value="" />
      </form>
    </div>
  );
}

export default CreateForm;
